'use strict';

var mongoose = require('bluebird').promisifyAll(require('mongoose'));

var GallerySchema = new mongoose.Schema({
  ename: Object,
  active: Boolean,
  event_id: String,
  image: Object,
  videos: Object,
  updatedAt: String,
  createdAt: String,
  modifiedBy: String
});

export default mongoose.model('Gallery', GallerySchema);
